import React from "react"
import { OptionsStyled } from "./Options.styled"
import { FontAwesomeIcon as FA } from "@fortawesome/react-fontawesome"
import { faAngleUp } from "@fortawesome/pro-regular-svg-icons"
import { faSort } from "@fortawesome/pro-solid-svg-icons"

const OptionsToggle = function (props) {
  /*
   * User "container" state/props/methods!
   * This "child" component is to simplify container's render() logic.
   */
  let that = props.that
  /*
   * View
   */
  return (
    <OptionsStyled className="OptionsToggle">
      <div className="options clickable" onClick={that.toggleOptions}>
        {that.state.showOptions ? (
          <>
            Hide options&nbsp;
            <FA icon={faAngleUp} />
            &nbsp;&nbsp;
          </>
        ) : (
          <>
            More options&nbsp;&thinsp;
            <FA icon={faSort} className="hide-small" />
          </>
        )}
      </div>
    </OptionsStyled>
  )
}

export default OptionsToggle
